<template>
<!--
    <div class="inputBox shadow">
    할일입력 : <input type="text" v-model="todo.content" placeholder="할일 입력" >
        기한 : <input type="date" v-model="todo.endDate" placeholder="기한">
        <span class="addContainer" @click="addTodo">
            <div class="addBtn fas fa-plus" aria-hidden='true'></div>
        </span>
    </div>
-->
    <div>
    <table>
      <tbody>
        <tr>
          <td>제목</td>
          <td>
            <input type="text" v-model="todo.qna_title" />
          </td>
        </tr>
        <tr>
          <td>내용</td>
          <td>
            <textarea  v-model="todo.qna_content" />
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <span class="addContainer" @click="addTodo">
              <div class="addBtn fas fa-plus" aria-hidden="true"></div>
            </span>
          </td>
        </tr>
      </tbody>
    </table>
    </div>


</template>

<script>
import Constant from '../Constant';
export default {
    data() {
        return {
            todo : {
                qna_title: '',
                qna_content: '',
                qna_datetime:'',
                qna_no: 0,
                qna_userid: ''
            }
        }
    },
    methods: {

        addTodo(){
            if(this.todo.qna_title.trim() != ''){
                console.log('할일 추가 :: ');
                console.log(this.todo);
                this.$store.dispatch(Constant.ADD_TODO,{todo : this.todo});
                this.$router.push('/list');
            }else{
                console.log('공백입력.');
            }    
            this.clear();
        },
        clear(){
            this.todo = {
            };  //this.todo = {} 이렇게 하면 안나옴
        }
    }
}     
</script>

